.ui-modal-cover .cover {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: none;
}
.ui-modal-cover .window {
  position: absolute;
  top: 40px;
  left: 50%;
  width: 600px;
  margin-left: -300px;
  padding: 0 30px 30px 30px;
  overflow: hidden;
}
.ui-modal-cover .window-shadow {
  padding: 15px;
  background: #eee;
  border: 1px solid #b5b5b5;
  border-top: none;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.ui-modal-cover .window-shadow[data-is-hide="false"] {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.ui-modal-cover .window .footer {
  display: flex;
  margin-top: 20px;
}
.ui-modal-cover .window .footer .blank {
  flex: 1 1;
}
.ui-modal-cover .window .footer button {
  margin-right: 15px;
}
.ui-modal-cover .window .footer button:last-child {
  margin-right: 0;
}
