.app.pending {
  padding: 100px 20px 0;
  text-align: center;
}
.app-body {
  display: flex;
}
.app-body .app-main {
  flex: 1 1;
}
button.ant-btn {
  padding: 0 10px;
  height: 22px;
  font-size: 12px;
  color: #000;
  min-width: 60px;
  -webkit-transition: none;
  transition: none;
}
button.ant-btn:hover,
button.ant-btn:focus {
  color: #000;
  border-color: #d9d9d9;
}
button.ant-btn:active {
  background: -webkit-gradient(linear, left top, left bottom, from(#6ca5fb), to(#086bff));
  background: -webkit-linear-gradient(top, #6ca5fb, #086bff);
  background: linear-gradient(to bottom, #6ca5fb, #086bff);
  border: none;
  box-shadow: none;
  color: #fff;
}
button.ant-btn.ant-btn-primary {
  background: -webkit-gradient(linear, left top, left bottom, from(#6ca5fb), to(#086bff));
  background: -webkit-linear-gradient(top, #6ca5fb, #086bff);
  background: linear-gradient(to bottom, #6ca5fb, #086bff);
  border: none;
  box-shadow: none;
  color: #fff;
}
button.ant-btn.ant-btn-primary:hover {
  color: white;
  border: none;
  background: -webkit-gradient(linear, left top, left bottom, from(#6ca5fb), to(#086bff));
  background: -webkit-linear-gradient(top, #6ca5fb, #086bff);
  background: linear-gradient(to bottom, #6ca5fb, #086bff);
}
input.ant-input {
  padding: 2px 4px;
  line-height: 1;
  height: unset;
  font-size: 12px;
  border-radius: 2px;
  border-color: #c6c6c6;
}
