header {
  height: 40px;
  background: -webkit-gradient(linear, left top, left bottom, from(#e5e5e5), to(#d0d0d0));
  background: -webkit-linear-gradient(top, #e5e5e5, #d0d0d0);
  background: linear-gradient(to bottom, #e5e5e5, #d0d0d0);
  border-bottom: 1px solid #a9a9a9;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 20px;
}
header button {
  background: #fff;
  border: 1px solid #cdcdcd;
  border-bottom-color: #b1b1b1;
  border-radius: 4px;
  outline: none;
  margin-right: 10px;
}
header button:active {
  background: #e1e1e1;
}
header button:disabled {
  color: #b6b6b6;
}
header button:disabled:active {
  background: #fff;
}
header .left-buttons,
header .right-buttons {
  display: flex;
  flex: 1 1;
}
header .page-title {
  font-size: 14px;
  font-weight: bold;
  flex: 1 1;
  text-align: center;
  cursor: default;
}
header .page-title i {
  color: #1B88FF;
}
