.app-sidebar {
  background: #e8e8e8;
  border-right: 1px solid #d0d0d0;
  width: 160px;
  height: calc(100vh - 40px);
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  overflow: hidden;
  flex-shrink: 0;
}
.app-sidebar[data-is-collapsed="true"] {
  width: 0;
}
.app-sidebar .sidebar-menu-group-title {
  padding: 5px 10px;
  color: #444;
  font-size: 12px;
  font-weight: bold;
  cursor: default;
}
.app-sidebar .sidebar-menu a {
  width: 160px;
  color: #444;
  padding: 5px 20px;
  font-size: 14px;
  display: block;
}
.app-sidebar .sidebar-menu a.active {
  background: #d0d0d0;
}
