.ui-issue {
  display: flex;
}
.ui-issue .check-box {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  border-radius: 50%;
  border: 2px solid #cacaca;
  margin-top: 10px;
}
.ui-issue .check-box .closed {
  width: 12px;
  height: 12px;
  background: #1B88FF;
  margin: 2px;
  border-radius: 50%;
}
.ui-issue .check-box[data-is-important="true"] {
  border-color: #F53E34;
}
.ui-issue .check-box[data-is-important="true"] .closed {
  background: #F7A43B;
}
.ui-issue .main {
  flex: 1 1;
  border-bottom: 1px solid #e9e9e9;
  line-height: 20px;
  padding: 10px 0;
  display: flex;
}
.ui-issue .main .title {
  margin-right: 5px;
  cursor: default;
  color: #000;
}
.ui-issue .main .title[data-is-closed="true"] {
  color: #aaa;
}
.ui-issue .main .labels {
  margin-right: 20px;
  display: flex;
  align-items: flex-start;
  flex: 1 1;
}
.ui-issue .main .labels .label {
  padding: 0px 5px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: bold;
  margin-right: 10px;
  border: 1px solid #fff;
}
.ui-issue .main .flag {
  width: 20px;
  color: #cacaca;
}
